@import url(https://fonts.googleapis.com/css2?family=Alegreya+Sans:wght@100;300;400;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Space+Mono:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Space+Mono:wght@400;700&display=swap);
@font-face {
    font-family: 'Alegreya Sans Black';
    font-style: normal;
    font-weight: normal;
    src: local('Alegreya Sans Black'), url(/millionaire/static/media/AlegreyaSans-Light.e2872092.ttf) format('ttf');
}

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #000;
    background-color: var(--primary-black-color);
    color: white;
}

code {
    font-family: Alegreya Sans Black, source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
    --primary-black-color: #000;
    --light-green-color: #cfff00;
    --light-pink-color: #ff03d7;
    --light-yellow-color: #fffb00;
}

