.App_app__2CsLU {
    margin: 0 30px;
    height: 100vh;

    display: grid;
    grid-template-areas:
          "header header header"
          "body body body"
          "footer footer footer";

    grid-template-rows: 60px 1fr 60px;
}

.App_app_header__1c6Ak {
    height: 60px;
    grid-area: header;
    text-align: center;
    font-family: 'Alegreya Sans', sans-serif;
    font-weight: bold;
    text-transform: uppercase;
    font-size: 3.5rem;
    color: var(--light-green-color);
}

.App_app_main__2WMHL {
    grid-area: body;
    display: grid;
    grid-template-areas:
          "nav body";
    grid-template-columns: 30% 70%;
}

.App_nav__1GqYr {
